import React from 'react';
import { Card, CardImg, CardText, CardBody, CardTitle, CardSubtitle, CardLink } from 'reactstrap';

const projectsData = [
    { name: '', description: 'logo', photo: '/mit-fullstack/assets/bank1/bank1.png' },
    { name: '', description: '0', photo: '/mit-fullstack/assets/bank1/bank2.png' },
    { name: '', description: '', photo: '/mit-fullstack/assets/bank1/bank3.png' },
    { name: '', description: '', photo: '/mit-fullstack/assets/bank1/bank4.png' }

];
const showProjects = projectsData.map((projectItem) => {
    return (
        <Card style={{ width: '300px', float: 'left', margin: '10px 20px' }}>
            <img
                alt="Card cap"
                src={projectItem.photo}
                height="300px"
            />
            <CardBody>
                <CardTitle tag="h5">
                    {projectItem.name}
                </CardTitle>
                <CardText dangerouslySetInnerHTML={{ __html: projectItem.description }}>
                </CardText>

            </CardBody>
        </Card>
    )
})

function Bankapp1(props) {
    return (
        <div className="container">
            <div className="row">
            {showProjects}
            <Card style={{ width: '600px', float: 'left', margin: '10px 20px' }}>
                <img
                    alt="Card cap"
                    src={'/mit-fullstack/assets/bank1/bank5.png'}
                    height="300px"
                />
                <CardBody>
                    <CardTitle tag="h5">
                        
                    </CardTitle>
                    <CardText>
                        
                    </CardText>
                </CardBody>
            </Card>
            </div>
            <div className='row'>
            <a target={'_blank'} href="https://liminjun.github.io/bankapp" class="btn btn-default" tabindex="1" role="button"></a>

            <a target={'_blank'} href="https://github.com/liminjun/bankapp" class="btn btn-primary" tabindex="2" role="button"></a>
            </div>
        </div>
    );
}

export default Bankapp1;